.row{
    margin-left: 20px;
    color: #fff;

}
.posters{
    display: flex;
    padding: 20px;
    overflow-x:scroll ;
    overflow-y: hidden;
}
.posters::-webkit-scrollbar
{
    display:none;
}
.poster{ 
    cursor: pointer;
   height:200px ;
    margin-right: 10px;
}
.smallPoster{
    cursor: pointer;
    max-height: 100px;
    margin-right: 10px;
}
.poster:hover{
    transform: scale(1.1);
}